export default async function BuildingDetailPage({ params }: BuildingDetailPageProps) {
  const buildingId = params.id;
  const [building, currentUser] = await Promise.all([
    getBuildingById(buildingId),
    getUserSession()
  ]);

  if (!building) {
    notFound();
  }

  return (
    <div className="space-y-8">
      <Link href="/" className="inline-flex items-center text-sm text-primary hover:underline mb-4">
        <ArrowLeft className="mr-1 h-4 w-4" />
        返回首页
      </Link>

      <Card className="overflow-hidden shadow-xl">
        <div className="grid md:grid-cols-2 gap-0">
          <div className="relative aspect-[4/3] md:aspect-auto">
            <Image
              src={building.imageUrl}
              alt={building.name}
              layout="fill"
              objectFit="cover"
              priority
              data-ai-hint={building.aiHint || "minecraft architecture"}
              className="md:rounded-l-lg"
            />
          </div>
          <div className="flex flex-col">
            <CardHeader className="pb-4">
              <CardTitle className="text-3xl lg:text-4xl font-bold text-primary">{building.name}</CardTitle>
              <div className="flex flex-wrap gap-4 items-center mt-2">
                <CardDescription className="flex items-center text-muted-foreground text-base">
                  <UserIcon className="mr-1.5 h-5 w-5" /> 作者: {building.author}
                </CardDescription>
                <Badge variant="default" className="text-base px-3 py-1 flex items-center">
                  <Scaling className="mr-2 h-4 w-4" />
                  规格: {building.size}
                </Badge>
              </div>
            </CardHeader>
            <CardContent className="flex-grow pt-0">
              <Separator className="my-4" />
              <p className="text-foreground/90 leading-relaxed text-base">{building.description}</p>
            </CardContent>
            <CardFooter className="flex flex-col sm:flex-row justify-between items-center gap-4 pt-6 border-t bg-muted/30 md:rounded-br-lg p-6">
              <div className="flex items-center text-accent text-xl">
                <ThumbsUp className="mr-2 h-6 w-6" />
                <span className="font-semibold">{building.votes}</span>
                <span className="ml-1.5 text-base">票</span>
              </div>
              <VoteButton buildingId={building.id} currentUser={currentUser} initialVotes={building.votes} />
            </CardFooter>
          </div>
        </div>
      </Card>

      <SizeSpecificationTable />

    </div>
  );
}
